
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

body {
  margin: 0;
  padding: 0;
}
#map {
  position: absolute;
  top: 0;
  bottom: 0;
  width: 100%;
}
#inputs {
  position: absolute;
  top: 0;
  left: 0;
  padding: 10px;
}
#menu {
  position: absolute;
  background: #fff;
  padding: 20px;
  margin: 20px;
  font-family: "Open Sans", sans-serif;
  max-width: 180px;
  top: 20px;
  left: -9px;
}
#worldview-options {
  margin-top: 10px;
}
.tooltip {
  position: relative;
  display: inline-block;
  border-bottom: 1px dotted #ccc;
  color: #006080;
}
.tooltip .tooltiptext {
  visibility: hidden;
  position: absolute;
  width: 120px;
  background-color: #555;
  color: #fff;
  text-align: center;
  padding: 5px 0;
  border-radius: 6px;
  z-index: 1;
  opacity: 0;
  -webkit-transition: opacity 0.6s;
  transition: opacity 0.6s;
}
.tooltip:hover .tooltiptext {
  visibility: visible;
}
.tooltip-bottom {
  top: 135%;
  left: 50%;
  margin-left: -60px;
}
